import React, { useState } from 'react';
import { requiredRule } from '../../../utils/validateRules';
import CrudPage from '../../../components/common/CrudPage';
import {
  deleteTrainingScoreQuestion,
  getRootTrainingScoreQuestionDetail,
  updateChildTrainingScoreQuestion,
} from '../../../services/studentPortal';
import TextEditor from '../../../components/common/Input/TextEditor';
import InputOnlyNumber from '../../../react-uikit/component/input/InputOnlyNumber';

export default () => {
  const [rootQuestion, setRootQuestion] = useState({});
  const model = [
    { field: 'id', name: 'Id' },
    { field: 'label', name: 'Tn', rules: [requiredRule], formItem: <TextEditor /> },
    { field: 'maxGrade', name: 'im ti a', rules: [requiredRule], formItem: <InputOnlyNumber /> },
    { field: 'order', name: 'V tr', rules: [requiredRule], formItem: <InputOnlyNumber /> },
  ];

  const config = {
    title: `${rootQuestion ? rootQuestion.label : 'Chi tit cu hi rn luyn'}`,
    serviceGetList: async ({ id }) => {
      let items = [] 
      if (!id) {
        return [{ data: { items } }, null];
      }
      const [res] = await getRootTrainingScoreQuestionDetail({ id });
      setRootQuestion(res ? res.data : null);
      items = res ? res.data.children : [];
      return [{ data: { items } }, null];
    },
    serviceCreate: updateChildTrainingScoreQuestion,
    serviceUpdate: updateChildTrainingScoreQuestion,
    serviceDelete: deleteTrainingScoreQuestion,
    tableColumns: [
      {
        title: 'Tn',
        dataIndex: 'label',
        render: text => {
          return <div dangerouslySetInnerHTML={{ __html: text }} />;
        },
      },
      'maxGrade',
      'order',
    ],
    saveFormItems: ['label', 'maxGrade', 'order'],
    saveFormCustomValue: values => {
      return { ...values, parentId: rootQuestion.id, yearEduId: rootQuestion.yearEduId, semesterId: rootQuestion.semesterId };
    },
  };
  return <CrudPage model={model} config={config} />;
};



